import Link from 'next/link'
import { FaTwitter, FaGithub, FaLinkedin, FaInstagram, FaFacebook, FaDiscord } from 'react-icons/fa'

export default function SocialLinks() {
  const socialLinks = [
    {
      href: "https://twitter.com",
      bgColor: "bg-blue-500",
      hoverColor: "hover:bg-blue-600",
      icon: <FaTwitter size={24} aria-label="Twitter" />
    },
    {
      href: "https://github.com",
      bgColor: "bg-gray-900",
      hoverColor: "hover:bg-gray-800",
      icon: <FaGithub size={24} aria-label="GitHub" />
    },
    {
      href: "https://linkedin.com",
      bgColor: "bg-blue-700",
      hoverColor: "hover:bg-blue-800",
      icon: <FaLinkedin size={24} aria-label="LinkedIn" />
    },
    {
      href: "https://instagram.com",
      bgColor: "bg-pink-600",
      hoverColor: "hover:bg-pink-700",
      icon: <FaInstagram size={24} aria-label="Instagram" />
    },
    {
      href: "https://facebook.com",
      bgColor: "bg-blue-800",
      hoverColor: "hover:bg-blue-900",
      icon: <FaFacebook size={24} aria-label="Facebook" />
    },
    {
      href: "https://discord.com",
      bgColor: "bg-indigo-700",
      hoverColor: "hover:bg-indigo-800",
      icon: <FaDiscord size={24} aria-label="Discord" />
    }
  ]

  return (
    <div className="mt-8 max-w-md mx-auto p-6 bg-white dark:bg-gray-900 rounded-lg shadow-lg">
      <h2 className="text-2xl font-bold mb-6 text-gray-900 dark:text-white text-center">Connect with me</h2>
      <div className="flex justify-center space-x-6">
        {socialLinks.map(({ href, bgColor, hoverColor, icon }) => (
          <Link
            key={href}
            href={href}
            target="_blank"
            rel="noopener noreferrer"
            className={`${bgColor} ${hoverColor} p-3 rounded-full transition-colors duration-300 shadow-md hover:shadow-lg flex items-center justify-center text-white`}
            aria-label={`Link to ${href}`}
          >
            {icon}
          </Link>
        ))}
      </div>
    </div>
  )
}
